.header {
  background: url("bg-pattern-intro-mobile.85d10c50.svg") no-repeat center center, linear-gradient(to right bottom, var(--gradient-light-red), var(--gradient-red) );
  height: max(80vh, 570px);
  border-bottom-left-radius: 100px;
}

.header__navbar--mobile {
  justify-content: space-between;
  align-items: center;
  padding: 48px 24px 0;
  display: flex;
}

.header__navbar--desktop {
  display: none;
}

.header__content {
  width: 100%;
  min-height: calc(100% - 88px);
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.header__title {
  font-family: var(--title-font);
  color: var(--white);
  text-align: center;
  font-size: 2rem;
  font-weight: 500;
}

.header__description {
  font-family: var(--body-font);
  color: var(--white);
  text-align: center;
  margin: 24px 0;
  font-size: 1.2rem;
  font-weight: 300;
}

.header__row {
  justify-content: center;
  gap: 16px;
  display: flex;
}

.header__link {
  font-family: var(--title-font);
  border: 1px solid var(--white);
  text-align: center;
  border-radius: 30px;
  padding: 16px;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.header__link:hover {
  filter: brightness(85%);
}

.header__link:focus {
  box-shadow: var(--focus-shadow);
  outline: none;
}

.header__link:active {
  opacity: .5;
}

.header__link--start {
  background-color: var(--white);
  color: var(--light-red);
}

.header__link--learn {
  color: var(--white);
}

.menu-lateral {
  width: 100vw;
  height: 95vh;
  background: none;
  padding: 48px 24px 0;
  transition: all .3s;
  position: absolute;
  top: -150vh;
  left: 0;
  overflow: auto;
}

.menu-lateral--open {
  transition: all .3s;
  top: 0;
}

.menu-lateral__header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu-lateral__content {
  background-color: var(--white);
  text-align: center;
  width: min(500px, 100%);
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  margin-top: 34px;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 30px;
  padding-right: 30px;
}

.menu-lateral__lista {
  list-style: none;
}

.menu-lateral__button {
  color: var(--darker-blue);
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  column-gap: 10px;
  margin-bottom: 24px;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

hr {
  background: var(--greyish-blue);
  width: 100%;
  height: 1px;
  margin-top: 35px;
  margin-bottom: 35px;
}

.menu-lateral__link {
  color: var(--darker-blue);
  font-size: 1.2rem;
  font-weight: 700;
}

.menu-lateral__sign-up {
  color: var(--white);
  background: linear-gradient(90deg, var(--gradient-light-red), var(--gradient-red) );
  border-radius: 50px;
  margin-top: 24px;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  font-size: 1.2rem;
  font-weight: 700;
  display: inline-block;
}

.navbar__third-list {
  background-color: var(--greyish-blue);
  filter: brightness(1.15);
  width: 100%;
  border-radius: 5px;
  flex-direction: column;
  gap: 20px;
  padding: 24px;
  list-style: none;
  display: none;
}

.third-list--open {
  display: flex;
}

.navbar__thirdlink {
  color: var(--darker-greyish-blue);
  filter: brightness();
  font-weight: 700;
}

.menu-lateral__arrow {
  transition: all .3s;
}

.rotate {
  transform: rotate(180deg);
}

.introduction {
  flex-direction: column;
  align-items: center;
  margin-top: 95px;
  display: flex;
}

.introduction__title {
  margin-bottom: 50px;
}

.introduction__illustration {
  width: min(500px, 100%);
  margin-left: auto;
  margin-right: auto;
}

.infra {
  width: 100%;
  background: url("bg-pattern-circles.d5778d88.svg") no-repeat bottom, linear-gradient(to right bottom, var(--gradient-dark-blue), var(--gradient-blue) );
  z-index: -1;
  border-radius: 0 100px;
  padding: 84px 24px;
  position: relative;
}

.infra__phones {
  width: min(768px, 100%);
  height: 400px;
  background: url("illustration-phones.021cbeaf.svg") center / contain no-repeat;
  margin-left: auto;
  margin-right: auto;
  transform: translateY(50%);
}

.infra__title {
  color: var(--white);
  margin-top: 150px;
  font-size: 2.5rem;
}

.infra__text {
  color: var(--white);
}

.advantages {
  margin-top: 95px;
}

.advantages > * {
  margin-bottom: 32px;
}

.advantages__laptop {
  width: min(500px, 100%);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.footer {
  background-color: var(--darker-black-blue);
  border-top-right-radius: 100px;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  padding: 32px 16px 40px;
  display: flex;
}

.footer__title {
  font-family: var(--title-font);
  color: var(--white);
  margin-bottom: 40px;
  font-size: 2.5rem;
  font-weight: 700;
}

.footer__subtitle {
  color: var(--white);
  font-family: var(--title-font);
  margin-bottom: 24px;
  font-size: 1.25rem;
}

.footer__links {
  text-align: center;
  margin-bottom: 24px;
  list-style: none;
}

.footer__links > :not(:last-of-type) {
  margin-bottom: 8px;
}

.footer__link-item {
  color: var(--greyish-blue);
  font-family: var(--body-font);
  font-size: 1.25rem;
  font-weight: 300;
  text-decoration: none;
}

.footer__link-item:hover {
  text-decoration: underline;
}

.footer__link-item:focus {
  outline: 1px solid var(--white);
  opacity: .5;
}

:-webkit-any(.footer__title, .footer__subtitle, .footer__link-item)::-webkit-selection {
  background-color: var(--light-red);
}

:is(.footer__title, .footer__subtitle, .footer__link-item)::selection {
  background-color: var(--light-red);
}

